import React from 'react';
import Row from './components/Row';
import TopBar from './components/TopBar';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {inputType: 'numbers', numberOfItems: '10', sortAlgorithmType: 'bubble sort',  items: [...Array(parseInt(10)).keys()] };
  }

callback_SADropdown = (SAType) => {
    this.setState({sortAlgorithmType: SAType});
}

callback_ITDropdown = (ITType) => {
    this.setState({inputType: ITType});
}

callback_NIDropdown = (NIType) => {
    this.setState({numberOfItems: NIType, items: [...Array(parseInt(NIType)).keys()]});
}

  render() {
    return (
      <div className="App">
        <button onClick={()=>{console.log(this.state.sortAlgorithmType); }}>Check State SAType</button>
        <button onClick={()=>{console.log(this.state.inputType); }}>Check State ITType</button>
        <button onClick={()=>{console.log(this.state.numberOfItems); }}>Check State NIType</button>
        <TopBar SAcallBack = {this.callback_SADropdown} NIcallBack = {this.callback_NIDropdown} ITcallBack = {this.callback_ITDropdown}/>
        
        <Row items={this.state.items}/>
      </div>
    );
  }
    
  
}

export default App;
