import React from 'react';
import './Row.css';
import Square from './Square.js';

class Row extends React.Component {
  constructor(props){
    super(props);
    
    
}   
  swap(e) {
    
    e.preventDefault();
       var name1 = "Item2";
      var name2 = "Item5";
      var first = document.getElementById(name1).innerHTML;
      var second = document.getElementById(name2).innerHTML;
      document.getElementById(name2).innerHTML = first;
      document.getElementById(name1).innerHTML = second;
    
  }

  
  render(){
    return (
      
      <div>        
        <div className="sortTable">         
          <Square items={this.props.items}/>
        </div>

          <div>
            <button onClick={ this.swap}>change Item</button>
          </div>
      </div>

      /*
      <div>        
        <table className="sortTable">
          <tr>
            
            <td className="sortItem" id="Item0">1</td>
              <td className="sortItem" id="Item1">2</td>
              <td className="sortItem" id="Item2">3</td>
              <td className="sortItem" id="Item3">4</td>
              <td className="sortItem" id="Item4">5</td>
              <td className="sortItem" id="Item5">6</td>
              <td className="sortItem" id="Item6">7</td>
              <td className="sortItem" id="Item7">8</td >
              <td className="sortItem" id="Item8">9</td>
              <td className="sortItem" id="Item9">10</td>    
            </tr>
          </table>

          <div>
            <button onClick={ this.swap}>change Item</button>
          </div>
      </div>*/


        
      );
  }
  
}

export default Row;

