import React from 'react';
import './Square.css';
class Square extends React.Component {
    render(){
        return(
            
            <div>
                {this.props.items.map(item => (
                    <div className="sortItem" id="Item0">
                        <p >{item}</p>
                    </div>
                ))}
            </div>
        
        );
    }
}

export default Square;